<template>
  <div class="container has-text-centered" id="percentagePlot"></div>
  <div id="tooltip"></div>
</template>

<script>
import * as d3 from "d3";

export default {
  name: 'PercentagePlot',
  props: ['labels', 'years', 'dataAsPercentage'],
  mounted() {
    window.addEventListener("resize", this.UpdateGraph);
    this.UpdateGraph();
  },
  watch: {
    values() {
      this.UpdateGraph();
    },
  },
  methods: {
    UpdateGraph() {
      document.getElementById("percentagePlot").innerHTML = "";

      var margin = { top: 50, right: 50, bottom: 50, left: 50 },
        width = document.getElementById("percentagePlot").offsetWidth * 0.9 - margin.left - margin.right,
        height = window.innerHeight * 0.75 - margin.top - margin.bottom;

      var tooltip = d3.select("#tooltip")
        .attr("class", "d3-tooltip")
        .style("position", "absolute")
        .style("top", "0")
        .style("left", "0")
        .style("visibility", "hidden")
        .style("padding", "15px")
        .style("background", "rgba(0,0,0,0.6)")
        .style("border-radius", "5px")
        .style("color", "#fff")

      // append the svg object to the body of the page
      var svg = d3.select("#percentagePlot")
        .append("svg")
        .attr("width", width + margin.left + margin.right)
        .attr("height", height + margin.top + margin.bottom)
        .append("g")
        .attr("transform",
          "translate(" + margin.left + "," + margin.top + ")");

      // X axis
      var x = d3.scaleBand()
        .range([0, width])
        .domain(this.years)
        .padding(0.2);

      svg.append("g")
        .attr("transform", "translate(0," + height + ")")
        .call(d3.axisBottom(x))
        .selectAll("text")
        .style("text-anchor", "middle");

      // Add Y axis
      var y = d3.scaleLinear()
        .domain([0, 100])
        .range([height, 0]);

      svg.append("g")
        .call(d3.axisLeft(y));

      var color = d3.scaleOrdinal()
        .domain(this.labels)
        .range(['#c4bd09', '#114803', '#27cb73', '#5033ad', '#bf21c5', '#4eed92', '#a0be3f', '#5a5831', '#c89cc9', '#846240'])

      var stackedData = d3.stack()
        .keys(this.labels)(Object.values(this.dataAsPercentage));
      // Show the bars
      svg.append("g")
        .selectAll("g")
        // Enter in the stack data = loop key per key = group per group
        .data(stackedData)
        .enter().append("g")
        .attr("fill", function (d) { return color(d.key); })
        .selectAll("rect")
        // enter a second time = loop subgroup per subgroup to add all rectangles
        .data(function (d) { return d; })
        .enter().append("rect")
        .attr("x", function (d) { return x(d.data.group); })
        .attr("y", function (d) { return y(d[1]); })
        .attr("height", function (d) { return y(d[0]) - y(d[1]); })
        .attr("width", x.bandwidth())
        .on('mouseover', function (d, i) {
          if (d != null) {
            var subgroupName = d3.select(this.parentNode).datum().key;

            console.log(subgroupName);
            console.log(i);
            tooltip.html(`${subgroupName}: ${i[1] - i[0]}%`).style("visibility", "visible");

            d3.select(this).transition()
              .duration('50')
              .attr('opacity', '.85');
            tooltip.transition().duration(50).style("visibility", "visible");
          }
        }).on("mousemove", function (e) {
          tooltip
            .style("top", (e.clientY - 10) + "px")
            .style("left", (e.clientX + 10) + "px");
        })
        .on('mouseout', function () {
          d3.select(this).transition()
            .duration('50')
            .attr('opacity', '1');

          tooltip.transition().duration(50).style("visibility", "hidden");
        })


    }
  }
}



</script>

