<template>
  <section class="hero">
    <div class="hero-body">
      <p class="title">
        D3 Plot
      </p>
      <p class="subtitle">
        Peikai Tan
      </p>
    </div>
  </section>

  <div class="tabs is-centered">
    <ul>
      <li v-for="option in options" :key="option" @click="selectedMode = option" v-bind:class = "(selectedMode == option)?'is-active':''"><a>{{option}}</a></li>
    </ul>
  </div>

  <section>
    <BarPlot :values="this.data" :years="this.years" v-if="(selectedMode == 'Bar Chart')" />
    <PercentagePlot :dataAsPercentage="this.dataAsPercentage" :labels="this.labels" :years="this.years" v-if="(selectedMode == 'Percentage Stacked Bar Chart')" />
  </section>


</template>

<script>
import BarPlot from './components/BarPlot.vue'
import PercentagePlot from './components/PercentagePlot.vue'

import axios from "axios";

const ENDPOINT = "https://data.gov.sg/api/action/datastore_search?resource_id=83c21090-bd19-4b54-ab6b-d999c251edcf";


export default {
  name: 'App',
  components: {
    BarPlot,
    PercentagePlot,
  },
  mounted() {
    axios.get(ENDPOINT).then(response => {
      this.ParseData(response);
    });
  },
  data() {
    return {
      years: [],
      labels: [],
      dataAsPercentage: {},
      data: {},
      options: ["Bar Chart", "Percentage Stacked Bar Chart"],
      selectedMode: "Bar Chart",
    }
  },
  methods: {
    ParseData(response) {
      this.data = {};

      response = response.data.result.records;

      // For each row, by year, create the individual type : value pairs
      var row;
      var year;
      var type;
      for (var index in response) {
        row = response[index];
        year = row.year;
        type = row.level_2;

        // Create label entry if it doesnt exist
        if (!this.labels.includes(type))
        {
          this.labels.push(type);
        }

        // Create entry if it doesnt exist
        if (this.data[year] == undefined) {
          this.data[year] = {};
          this.years.push(parseInt(year));
          
          this.dataAsPercentage[year] = {};
          this.dataAsPercentage[year]["group"] = parseInt(year);
        }

        // Create key value pair for entry
        this.data[year][type] = parseInt(row.value);
      }

      for (year in this.data)
      {
        const total = Object.values(this.data[year]).reduce((partialSum, a) => partialSum + a, 0);

        for (type in this.data[year])
        {
          this.dataAsPercentage[year][type] = Math.round(this.data[year][type] / total * 10000) / 100; 
        }
      }
    }
  }
}
</script>

<style>
#app {
  height: 100vh;
  width: 100vw;
}
</style>
