<template>
  <div class="container">
    <div class="field is-horizontal">
      <div class="field-label is-normal">
        <label class="label">Crimes in the year</label>
      </div>
      <div class="field-body">
        <div class="field is-narrow">
          <div class="control">
            <div class="select">
              <select id="selectButton" v-model="selectedYear">
                <option v-for="year in years" :key="year">{{ year }}</option>
              </select>
            </div>
          </div>
        </div>
      </div>
    </div>
  </div>
  <div class="container has-text-centered" id="barPlot"></div>
  <div id="tooltip"></div>
</template>

<script>
import * as d3 from "d3";

export default {
  name: 'BarPlot',
  props: ['values', 'years'],
  data() {
    return {
      selectedYear: 0,
    }
  },
  mounted() {
    window.addEventListener("resize", this.UpdateGraph);
  },
  watch: {
    values() { this.RefreshValues(); },
    selectedYear() { this.UpdateGraph(); }
  },
  computed: {
    labels() {
      try {
        return Object.keys(this.values[this.selectedYear])
      }
      catch (error) {
        return [];
      }

    },
    entries() {
      try {
        return Object.entries(this.values[this.selectedYear]);
      }
      catch (error) {
        return {};
      }
    },
  },
  methods: {
    RefreshValues() {
      for (var year in this.years) {
        this.selectedYear = Math.max(this.years[year], this.selectedYear);
      }
      this.UpdateGraph();
    },
    UpdateGraph() {
      document.getElementById("barPlot").innerHTML = "";

      var margin = { top: 50, right: 50, bottom: 50, left: 50 },
        width = document.getElementById("barPlot").offsetWidth * 0.9 - margin.left - margin.right,
        height = window.innerHeight * 0.75 - margin.top - margin.bottom;


      var tooltip = d3.select("#tooltip")
        .attr("class", "d3-tooltip")
        .style("position", "absolute")
        .style("top", "0")
        .style("left", "0")
        .style("visibility", "hidden")
        .style("padding", "15px")
        .style("background", "rgba(0,0,0,0.6)")
        .style("border-radius", "5px")
        .style("color", "#fff")

      // append the svg object to the body of the page
      var svg = d3.select("#barPlot")
        .append("svg")
        .attr("width", width + margin.left + margin.right)
        .attr("height", height + margin.top + margin.bottom)
        .append("g")
        .attr("transform",
          "translate(" + margin.left + "," + margin.top + ")");

      // X axis
      var x = d3.scaleBand()
        .range([0, width])
        .domain(this.labels)
        .padding(0.2);

      svg.append("g")
        .attr("transform", "translate(0," + height + ")")
        .call(d3.axisBottom(x))
        .selectAll("text")
        .style("text-anchor", "middle");

      var max = 0;

      this.entries.forEach(function (el) {
        max = Math.max(max, parseInt(el[1]));
      })

      max = Math.ceil(max / 9 * 10 / 1000) * 1000;

      // Add Y axis
      var y = d3.scaleLinear()
        .domain([0, max])
        .range([height, 0]);

      svg.append("g")
        .call(d3.axisLeft(y));

      // Bars
      svg.selectAll("mybar")
        .data(this.entries)
        .enter()
        .append("rect")
        .attr("class", "bar")
        .attr("x", function (d) {
          return x(d[0])
        })
        .attr("width", x.bandwidth())
        .attr("fill", "#69b3a2")
        .attr("height", function (d) {
          return height - y(d[1])
        }) // always equal to 0
        .attr("y", function (d) {
          tooltip.html('Data: ' + d[1])
          return y(d[1])

        }).on('mouseover', function (d, i) {
          if (d != null) {
            tooltip.html(`${i[0]}: ${i[1]}`).style("visibility", "visible");

            d3.select(this).transition()
              .duration('50')
              .attr('opacity', '.85');
            tooltip.transition().duration(50).style("visibility", "visible");
          }
        }).on("mousemove", function (e) {
          tooltip
            .style("top", (e.clientY - 10) + "px")
            .style("left", (e.clientX + 10) + "px");
        })
        .on('mouseout', function () {
          d3.select(this).transition()
            .duration('50')
            .attr('opacity', '1');

          tooltip.transition().duration(50).style("visibility", "hidden");
        })
    }
  }
}



</script>

